"use client";

import { useState } from "react";

export default function UploadCard() {
  const [file, setFile] = useState(null);

  const handleFile = (e) => {
    setFile(e.target.files[0]);
  };

  return (
    <div className="w-full max-w-md p-8 bg-white/40 backdrop-blur-xl border border-gray-200 rounded-3xl shadow-lg flex flex-col items-center gap-6">
      <input
        type="file"
        accept=".csv, .xlsx"
        onChange={handleFile}
        className="hidden"
        id="file-upload"
      />
      <label
        htmlFor="file-upload"
        className="cursor-pointer px-10 py-4 bg-gradient-to-r from-indigo-400 via-pink-400 to-purple-400 text-white font-semibold rounded-2xl shadow-md hover:scale-105 transition transform text-lg"
      >
        {file ? "Change File" : "Upload Data"}
      </label>
      {file && <p className="text-gray-700 text-sm">{file.name}</p>}
      <button className="text-indigo-600 underline mt-2">Use Demo Data</button>
    </div>
  );
}
